.edit-time-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 100%;
    height: 100%;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.5);
    position: absolute;
    top: 0;
    left: 0;
}

.edit-time-container input[type="text"],
.edit-time-container input[type="date"] {
    -ms-flex: 1 1;
        flex: 1 1;
    padding: 6px 0px;
    border: none;
    border-bottom: solid 1px #333;
    outline: none;
}
/* .edit-time-container input[type="date"]::-webkit-inner-spin-button,
.edit-time-container input[type="date"]::-webkit-calendar-picker-indicator {
    display: none;
    -webkit-appearance: none;
} */

.edit-time {
    padding: 30px;
    padding-bottom: 20px;
    background-color: #fff;
    border-radius: 10px;
    position: relative;
    -webkit-box-shadow: 0px 15px 15px -10px rgba(0, 0, 0, 0.3);
            box-shadow: 0px 15px 15px -10px rgba(0, 0, 0, 0.3);
}

.edit-time .row {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 20px;
}


.edit-time .row:not(:first-child) {
    padding-top: 20px;
}

.edit-time .col {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.edit-time .col:first-child {
    width: 60px;
}

.edit-time .col:nth-child(2) {
    width: 300px;
}



.edit-time .btn {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 70%;
    min-height: 40px;
    padding: 5px 20px;
    border-radius: 20px;
    border: solid 1px #333;
    cursor: pointer;
    margin-top: 20px;
    -webkit-transition: 0.1s;
    -o-transition: 0.1s;
    transition: 0.1s;
}
.edit-time .btn.disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.edit-time .btn:not(.disabled):hover {
    background-color: #333;
    color: #fff;
}
.edit-time .close {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 10px;
    right: 10px;
    cursor: pointer;
}
.edit-time .close::before,
.edit-time .close::after {
    display: block;
    content: "";
    width: 2px;
    height: 100%;
    background-color: #333;
    border-radius: 2px;
    position: absolute;
    top: 50%;
    left: 50%;
}

.edit-time .close::before {
    -webkit-transform: translate(-50%, -50%) rotate(-45deg);
        -ms-transform: translate(-50%, -50%) rotate(-45deg);
            transform: translate(-50%, -50%) rotate(-45deg);
}
.edit-time .close::after {
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
        -ms-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
}

.edit-time .wave {
    padding: 0 5px;
}
.edit-time .delete {
    text-align: center;
    cursor: pointer;
    color: #666;
}

@media only screen and (max-width: 768px)   {
    .edit-time {
        padding: 20px;
    }
    .edit-time .row {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: start;
            align-items: flex-start;
        font-size: 12px;
    }
    .edit-time .col + .col  {
        margin-top: 5px;
    }
    .edit-time .col:nth-child(2) {
        width: 240px;
    }
    .edit-time .btn {
        width: 100%;
        min-height: 30px;
    }
    .edit-time .delete {
        width: 100%;
    }
}



