.time-item {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 100%;
    height: 100%;
    color: #fff;
    cursor: pointer;
    border-radius: 20px;
    -webkit-transition: 
        -webkit-box-shadow 0.2s,
        -webkit-transform 0.2s;
    transition: 
        -webkit-box-shadow 0.2s,
        -webkit-transform 0.2s;
    -o-transition: 
        box-shadow 0.2s,
        transform 0.2s
    ;
    transition: 
        box-shadow 0.2s,
        transform 0.2s
    ;
    transition: 
        box-shadow 0.2s,
        transform 0.2s,
        -webkit-box-shadow 0.2s,
        -webkit-transform 0.2s;
}
.time-item:hover {
    -webkit-box-shadow: 0px 2px 5px -3px #000;
            box-shadow: 0px 2px 5px -3px #000;
    -webkit-transform: translateY(-1px);
        -ms-transform: translateY(-1px);
            transform: translateY(-1px);
}

.time-item .text-container {
    width: calc(100% - 20px);
    white-space: nowrap;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    margin-left: 10px;
}


